import React, { createContext, useReducer, useEffect, useState } from "react";
import { wordReducer } from "../reducers/wordReducer";

export const WordContext = createContext();

const WordContextProvider = props => {
  const [wordsOfTheDay, setWordsOfTheDay] = useState([
    {
      word: "ludic",
      definition: "Of or relating to play or playfulness"
    }
  ]);

  const [vocab, dispatch] = useReducer(wordReducer, [], () => {
    const localData = localStorage.getItem("vocab");

    return localData ? JSON.parse(localData) : [];
  });

  useEffect(() => {
    localStorage.setItem("vocab", JSON.stringify(vocab));
  }, [vocab]);

  return (
    <WordContext.Provider value={{ vocab, wordsOfTheDay, dispatch }}>
      {props.children}
    </WordContext.Provider>
  );
};

export default WordContextProvider;
