import { v1 as uuidv1 } from "uuid";

export const wordReducer = (state, action) => {
  switch (action.type) {
    case "ADD_WORD":
      return [
        ...state,
        {
          word: action.vocab.word,
          definition: action.vocab.definition,
          id: uuidv1()
        }
      ];
    case "REMOVE_WORD":
      return state.filter(word => word.id !== action.id);
    default:
      return state;
  }
};
