import React, { useContext, useState } from "react";
import { WordContext } from "../contexts/WordContext";

const WordsOfTheDay = () => {
  const { wordsOfTheDay, dispatch } = useContext(WordContext);
  const [addBtn, setAddBtn] = useState("+");

  const addWordHandler = e => {
    dispatch({
      type: "ADD_WORD",
      vocab: {
        word: wordsOfTheDay[0].word,
        definition: wordsOfTheDay[0].definition
      }
    });

    setAddBtn("");
  };

  const closeWordHandler = () => {
    const closeBtnActive = document.querySelector(".words-of-the-day");

    closeBtnActive.classList.add("close-btn-active");
  };

  return (
    <div className="words-of-the-day">
      <>
        <h3>Word of the day</h3>
        <p className="word-of-the-day">{wordsOfTheDay[0].word}</p>
        <p>{wordsOfTheDay[0].definition}</p>
        <div className="add-btn" onClick={addWordHandler}>
          {addBtn}
        </div>
        <div className="close-btn" onClick={closeWordHandler}>
          &#8592;
        </div>
      </>
    </div>
  );
};

export default WordsOfTheDay;
