import React, { useContext } from "react";
import { WordContext } from "../contexts/WordContext";

const WordDetails = ({ vocab, wordsOfTheDay }) => {
  const { dispatch } = useContext(WordContext);

  return (
    <li>
      <div className="vocab">
        <div
          className="word"
          onClick={() => dispatch({ type: "REMOVE_WORD", id: vocab.id })}
        >
          {vocab.word}
        </div>
        <div className="definition">{vocab.definition}</div>
      </div>
    </li>
  );
};

export default WordDetails;
